.newsletter {
  background: map-get($colors, primary);
  padding: 60px 40px;
  overflow: hidden;
  @include breakpoint(desktop) {
    @include border-radius(0 100px 100px 100px);
    margin: 40px 0;
  }
  @include breakpoint(mobile) {
    padding: 40px 20px;
  }

  &--intro {
    text-align: center;
  }

  &--title {
    color: #fff;
    @include font-size(28);
    margin: 0 0 4px;
    @include breakpoint(mobile) {
      @include font-size(22);
    }
  }

  &--subtitle {
    @include font-size(16);
    font-weight: 300;
    color: #fff;
    line-height: 1.3;
    @include breakpoint(mobile) {
      @include font-size(18);
      line-height: 1.2;
    }
  }
}

.mailchimp-form {
  max-width: 450px;
  margin: 40px auto 0;

  &--buttons {
    text-align: center;
    margin-top: 24px;
  }

  .checkbox-form {
    color: #fff;
    margin: 16px 0;

    label {
      color: #fff;
    }
  }
}
