@mixin breakpoint($point) {
  @if $point == large {
    @media (min-width: 1440px) {
      @content;
    }
  } @else
  if $point == medium {
    @media (min-width: 1024px) {
      @content;
    }
  } @else
  if $point == mobile {
    @media (max-width: 640px) {
      @content;
    }
  } @else
  if $point == desktop {
    @media (min-width: 641px) {
      @content;
    }
  } @else
  if $point == tablet {
    @media (max-width: 1024px) {
      @content;
    }
  }
}
